import { formatNumberWithCommas } from "@/components/utils/formats/formatNumbers";
import Form from "@/components/views/invesment/deposit/Form";
import useAxios from "@/hooks/useAxios";
import MainLayout from "@/layouts/main";

const Deposit = () => {
  const { data } = useAxios<{ limit: number; deposits: number }>({
    url: "users/current-deposit",
    method: "get",
    defaultValue: {
      limit: 0,
      deposits: 0,
    },
  });

  const rest = (data?.limit || 0) - (data?.deposits || 0);
  return (
    <div>
      <div className="flex flex-col">
        <p className="font-bold text-xl">
          Invertir gener√° hasta el 8% de rendimiento mensual
        </p>
        <small>Puedes invertir hasta 10x el valor de tu membresia</small>
        <small>Actual: ${formatNumberWithCommas(data?.deposits, 0)} USD</small>
        <small>Restante: ${formatNumberWithCommas(rest, 0)} USD</small>
      </div>
      <div className="mt-8">
        <Form />
      </div>
    </div>
  );
};

const Container = () => {
  return (
    <MainLayout>
      <Deposit />
    </MainLayout>
  );
};

export default Container;
