/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable @next/next/no-img-element */
import { Button, Input } from "@nextui-org/react";
import { QR } from "./Form";
import axiosInstance from "@/services";
import { useTranslation } from "react-i18next";
import useClipboard from "@/hooks/useClipboard";
import { FiCopy } from "react-icons/fi";

const currencyIcon = {
  USDT: (
    <img
      src="/img/tether.png"
      height={12}
      width={12}
      className="h-[12px] w-[12px]"
    />
  ),
};

type Props = {
  qr: QR;
  onCancel: () => void;
};

const ShowQR = (props: Props) => {
  const { t } = useTranslation();
  const { copy } = useClipboard();

  const showConfirmationDialog = () => {
    cancel();
  };

  const cancel = async () => {
    await axiosInstance.delete("users/qr-deposit");
    props.onCancel();
  };

  return (
    <div className="bg-zinc-900 p-4 w-max flex flex-col gap-4 rounded-lg justify-center items-center">
      <div>
        <img
          src={props.qr.qrcode_url}
          className={"h-[200px] w-[200px]"}
          alt="QR CODE"
        />
      </div>

      <Input
        readOnly
        value={props.qr.amount}
        label="Cantidad (USDT)"
        labelPlacement="outside"
        endContent={
          <div className="flex items-center space-x-2">
            <span>{"USDT"}</span>{" "}
            <div
              className="bg-zinc-900 p-2 rounded-lg hover:cursor-pointer hover:bg-zinc-500"
              onClick={() => copy(props.qr.amount)}
            >
              <FiCopy />
            </div>
          </div>
        }
      />

      <Input
        readOnly
        value={props.qr.address}
        label="Wallet"
        labelPlacement="outside"
        endContent={
          <div className="flex items-center space-x-2">
            <div
              className="bg-zinc-900 p-2 rounded-lg hover:cursor-pointer hover:bg-zinc-500"
              onClick={() => copy(props.qr.address)}
            >
              <FiCopy />
            </div>
          </div>
        }
      />

      <div>
        <Button color="danger" onPress={showConfirmationDialog}>
          {t("cancel")}
        </Button>
      </div>
    </div>
  );
};

export default ShowQR;
