import { useState } from "react";
import { Form, Input, Checkbox, Button } from "@heroui/react";
import useAxios from "@/hooks/useAxios";
import { Spinner } from "@nextui-org/react";
import ShowQR from "./ShowQR";
import axiosInstance from "@/services";
import dayjs from "dayjs";
import { useTranslation } from "react-i18next";

export type QR = {
  address: string;
  amount: string;
  membership_type: string | null;
  status: string;
  expires_at: any;
  qrcode_url: string;
};

const InvestmentForm = () => {
  const { t } = useTranslation();
  const [errors, setErrors] = useState<Record<string, any>>({});
  const [sendLoading, setLoading] = useState(false);

  const { data, loading, refetch } = useAxios<{ qr: null | QR }>({
    url: "users/qr-deposit",
    method: "get",
    defaultValue: {},
  });

  const isExpired = data?.qr
    ? dayjs(data.qr.expires_at).isBefore(dayjs())
    : false;

  const onSubmit = async (e: any) => {
    e.preventDefault();
    const data: Record<string, any> = Object.fromEntries(
      new FormData(e.currentTarget)
    );

    // Custom validation checks
    const newErrors = {};

    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors);

      return;
    }

    if (Number(data.amount) < 100) {
      setErrors({ terms: "Cantidad minima 100 USD" });

      return;
    }

    if (data.terms !== "true") {
      setErrors({ terms: "Acepta los terminos y condiciones" });

      return;
    }

    // Clear errors and submit
    setErrors({});

    try {
      setLoading(true);
      await axiosInstance.post("users/create-qr-deposit", {
        amount: data.amount,
      });

      refetch();
    } catch (err) {
      setErrors({
        network: "Hubo un problema en el servidor, intenta mas tarde",
      });
    } finally {
      setLoading(false);
    }
  };

  if (loading) return <Spinner />;

  if (data?.qr && !isExpired) {
    return <ShowQR qr={data.qr} onCancel={refetch} />;
  }

  return (
    <div className="bg-zinc-900 w-max p-4 rounded-lg">
      <Form
        className="w-full space-y-8"
        validationBehavior="native"
        validationErrors={errors}
        onSubmit={onSubmit}
      >
        <div className="flex flex-col gap-4 max-w-md">
          <Input
            isRequired
            errorMessage={({ validationDetails }) => {
              if (validationDetails.valueMissing) {
                return "Please enter your email";
              }
              if (validationDetails.typeMismatch) {
                return "Please enter a valid email address";
              }
            }}
            label={t("invest_view.amount")}
            labelPlacement="outside"
            name="amount"
            placeholder={t("invest_view.amount_deposit")}
            type="text"
          />

          <Checkbox
            isRequired
            classNames={{
              label: "text-small",
            }}
            isInvalid={!!errors.terms}
            name="terms"
            validationBehavior="aria"
            value="true"
            onValueChange={() =>
              setErrors((prev) => ({ ...prev, terms: undefined }))
            }
          >
            {t("accept_terms")}
          </Checkbox>

          {errors.terms && (
            <span className="text-danger text-small">{errors.terms}</span>
          )}

          <div className="flex gap-4">
            <Button
              className="w-full bg-gradient-to-r from-blue-200/50 to-pink-200/50 text-black font-bold"
              color="primary"
              type="submit"
              isLoading={sendLoading}
            >
              {t("invest_view.generate_qr")}
            </Button>
          </div>

          {errors.network && (
            <span className="text-danger text-small">{errors.network}</span>
          )}
        </div>
      </Form>
    </div>
  );
};

export default InvestmentForm;
